.userInfo {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px; /* Larger text for user info */
}

.logoutButton {
    padding: 10px 20px;
    font-size: 16px; /* Larger text for logout button */
    background-color: #db4437; /* Google's brand red */
    color: white;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 10px; /* Space between the greeting text and button */
    transition: background-color 0.3s ease;
}

.logoutButton:hover {
    background-color: #a33224; /* Darken the red on hover */
}
